<template>
    <transition name='fade'>
      <div class='messagePage'>
        <v-Header class="head" :comeBack='comeBack'>
          <span slot='text'>我的信息</span>
        </v-Header>
        <div class='messageScroll'>
          <div class='messageListsCon' v-for='item in lists'>
            <div class='messageList'>
              <div class='headImg' v-if='item.headUrl' :style="{backgroundImage:'url('+item.headUrl+')'}"></div>
              <div class='listInfo'>
                <span class='name' v-text='item.name'></span>
                <span class='text' v-text='item.text'></span>
                <span class='time' v-text='item.time'></span>
              </div>
              <div class='listImg' v-if='item.imgUrl' :style="{backgroundImage:'url('+item.imgUrl+')'}"></div>
            </div>
          </div>
        </div>
      </div>
    </transition>
</template>
<script>
import Header from '../../components/header';
export default {
    name:'',
    props:['comeBack'],
    components: {'v-Header':Header},
    data(){
        return {
            lists:[
              {
                headUrl:'https://images.gitee.com/uploads/5/1889405_yangjialiang.png?1533865770',
                imgUrl:'https://s33.postimg.cc/acjanzcm7/image.jpg',
                name:'Kiko',
                text:'论坛只有正文所以这是正文而且该货没加图',
                time:'1分钟'
              },
              {
                headUrl:'https://images.gitee.com/uploads/5/1889405_yangjialiang.png?1533865770',
                imgUrl:'https://s33.postimg.cc/acjanzcm7/image.jpg',
                name:'Kiko',
                text:'论坛只有正文所以这是正文而且该货没加图',
                time:'1分钟'
              },
              {
                headUrl:'https://images.gitee.com/uploads/5/1889405_yangjialiang.png?1533865770',
                imgUrl:'https://s33.postimg.cc/acjanzcm7/image.jpg',
                name:'Kiko',
                text:'论坛只有正文所以这是正文而且该货没加图',
                time:'1分钟'
              },
              {
                headUrl:'https://images.gitee.com/uploads/5/1889405_yangjialiang.png?1533865770',
                imgUrl:'https://s33.postimg.cc/acjanzcm7/image.jpg',
                name:'Kiko',
                text:'论坛只有正文所以这是正文而且该货没加图',
                time:'1分钟'
              }
            ]
        }
    }
}
</script>

<style lang="scss" scoped>
    *{
      border: none;
      margin: 0;
      padding: 0;
    }
    .messagePage{
      width:100%;
      height:100%;
      position: absolute;
      top:0;
      left:0;
      overflow: hidden;
      background:white;
      .messageScroll{
        position: absolute;
        top:0.82rem;
        left:0;
        right:0;
        bottom:0;
        margin:auto;
        overflow-x: hidden;
        overflow-y: scroll;
        -webkit-overflow-scrolling:touch;
        .messageListsCon{
          width:100%;
          height:1.5rem;
          border-bottom: 0.01rem solid rgb(229,229,229); 
          display:flex;
          align-items:center;
          justify-content: center;
          .messageList{
            width:7.1rem;
            height:1.2rem;
            display:flex;
            .headImg{
              width:0.9rem;
              height:0.9rem;
              background-size:cover;
              background-position:center;
              border-radius:0.9rem;
              margin-right:0.23rem;
            }
            .listImg{
              width:1.2rem;
              height:1.2rem;
              background-size:cover;
              background-position:center;
              margin-left:0.2rem;
            }
            .listInfo{
              flex:1;
              display: flex;
              flex-direction: column;
              justify-content:space-between;
              overflow: hidden;
              line-height: initial;
              .name{color:#1797c6;font-size:0.24rem;}
              .text{color:#333333;font-size:0.3rem;overflow:hidden;white-space: nowrap;
              text-overflow: ellipsis;}
              .time{color:#888888;font-size:0.24rem;}
            }
          }
        }
      }
    }
</style>


