<template>
  <transition name='fade'>
    <div class='myClubPage'>
      <v-Header class="head" :comeBack='comeBack'>
        <span slot='text'>我的俱乐部</span>
      </v-Header>
      <div id='listsScrollCon' class='listsScrollCon'>
      <nav class='navbar'>
        <div :class="['item',type=='myClub'&&'active']" @click='changeType("myClub")'>我管理的俱乐部（{{myclubList.length}}）</div>
        <div :class="['item',type!=='myClub'&&'active']"  @click='changeType("joinClub")'>我加入的俱乐部（{{joinClubList.length}}）</div>
        <div :class="['line',type=='myClub'&&'active']"></div>
      </nav>
      <div :class="['myclub',type=='myClub'&&'active']">
        <div class='clubCon'>
          <div class='clubCon' v-if='myclubList.length>0'>
            <div v-for='info in myclubList'>
              <mt-cell class="list" :title="info.name" is-link to='/club'>
                  <img slot="icon" :src="info.imgUrl" class='userHeader'>
              </mt-cell>
            </div>
          </div>
        </div>
        <div class='publishBtn'>创建俱乐部</div>
      </div>
      <div :class="['joinclub',type!='myClub'&&'active']">
        <div class='clubCon' v-if='joinClubList.length>0'>
          <div v-for='info in joinClubList'>
            <mt-cell class="list" :title="info.name" is-link to='/club'>
                <img slot="icon" :src="info.imgUrl" class='userHeader'>
            </mt-cell>
          </div>
        </div>
      </div>
      </div>
    </div>
  </transition>
</template>
<script>
  import Header from '../../components/header';
  export default {
      name: '',
      components: {'v-Header':Header},
      props: ['comeBack'],
      data () {
        return {
          type:'myClub',
          myclubList:[],
          joinClubList:[
            {
              name:'晴天霹雳俱乐部',
              imgUrl:'https://images.gitee.com/uploads/5/1889405_yangjialiang.png?1533865770',
            },
            {
              name:'Sasa俱乐部',
              imgUrl:'https://images.gitee.com/uploads/5/1889405_yangjialiang.png?1533865770',
            }
          ]
        }
      },
      watch: {},
      computed: {
      },
      created () {
        this.selected=2;
      },
      methods: {
        changeType (data) {
          this.type=data;
        }
      }
    }
</script>
<style lang="scss" scoped>
*{
  border: none;
  margin: 0;
  padding: 0;
}
.list{
  height: 1rem;
  line-height: 1rem;
  font-size: 0.32rem;
  color: #333333;}
.myClubPage{
  width:100%;
  height:100%;
  position: absolute;
  top:0;
  left:0;
  overflow: hidden;
  background:white;
  .listsScrollCon{
    position: absolute;
    top:0.82rem;
    left:0;
    right:0;
    bottom:0;
    margin:auto;
    overflow: hidden;
    .navbar{
      position:relative;
      width:7.5rem;
      height:0.86rem;
      display:flex;
      
      .item{
        flex:1;
        font-size:0.32rem;
        line-height:0.86rem;
        text-align:center;
        color:#666666;
        &.active{
          color:#1797c6;
        }
      }
      .line{
        width: 50%;
        position: absolute;
        left: 0;
        bottom: 0;
        border-bottom: solid #1797c6 1px;
        transform:translate(100%,0);
        transition:transform 0.3s;
        &.active{
          transform:translate(0%,0);
        }
      }
    }
  }
  .myclub,.joinclub{
    position: absolute;
    top:0.87rem;
    left:0;
    right:0;
    bottom:0;
    margin:auto;
    overflow: hidden;
  }
  .joinclub{
    transform:translate(100%,0);
    transition:transform 0.3s;
    &.active{
      transform:translate(0%,0);
    }
  }
  .myclub{
    transform:translate(-100%,0);
    transition:transform 0.3s;
    &.active{
      transform:translate(0%,0);
    }
    .clubCon{
      bottom:0.88rem;
      
    }
  }
  .userHeader{
        width:0.76rem;
        height:0.76rem;
        border-radius: 0.76rem;
        margin:0 0.4rem;
      }
    .clubCon{
      position: absolute;
      top:0;
      left:0;
      right:0;
      bottom:0;
      margin:auto;
      overflow-x: hidden;
      overflow-y: scroll;
      -webkit-overflow-scrolling:touch;
      
    }
  
  .publishBtn{
      width:100%;
      height:0.88rem;
      position:absolute;
      bottom:0;
      left:0;
      background:url('../../assets/images/publishBtn.jpg') no-repeat;
      background-size:cover;
      color:white;
      line-height:0.88rem;
      font-size:0.36rem;
      text-align:center;
    }
}
</style>


